import {SystemEntryPropsParam,SystemEntryFallback} from "../SystemEntry";
import SystemFile from "./SystemFile";


export const SystemFileCallback={
    name:"callback",
    type:"(sysfile:SystemFile):void",
};

export default {
    id:"useSystemFile",
    type:"function",
    description:`
        A simpler way to use cordova-plugin-file API 
        @Creates the file if not found
        @returns a promise so you can use await instead of the callback/fallback params
        @Browser support is for development only. Uses local storage items as files
        @requires <a href="https://cordova.apache.org/docs/en/12.x/reference/cordova-plugin-file/index.html">cordova-plugin-file</a>
    `,
    preview:{
        title:"typical usage",
        code:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">useSystemFile</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"cordova-vritra"</span>;</div><br><br><div><span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">onDeviceReady</span>(){</div><div>&nbsp; &nbsp; <span style="color: #dcdcaa;">useSystemFile</span>(<span style="color: #ce9178;">`</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">cordova</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">file</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">externalDataDirectory</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">/TextFile.txt`</span>,<span style="color: #569cd6;">async</span> (<span style="color: #9cdcfe;">sysfile</span>)<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #9cdcfe;">sysfile</span>.<span style="color: #dcdcaa;">write</span>(<span style="color: #ce9178;">"This is a first sentence."</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #9cdcfe;">sysfile</span>.<span style="color: #dcdcaa;">append</span>(<span style="color: #ce9178;">" This is an appended sentence."</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #9cdcfe;">sysfile</span>.<span style="color: #dcdcaa;">rename</span>(<span style="color: #ce9178;">"NewTextFile"</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #9cdcfe;">sysfile</span>.<span style="color: #dcdcaa;">moveTo</span>(<span style="color: #9cdcfe;">cordova</span>.<span style="color: #9cdcfe;">file</span>.<span style="color: #9cdcfe;">externalCacheDirectory</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">content</span><span style="color: #d4d4d4;">=</span><span style="color: #c586c0;">await</span> <span style="color: #9cdcfe;">sysfile</span>.<span style="color: #dcdcaa;">readAsText</span>();</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #dcdcaa;">alert</span>(<span style="color: #4fc1ff;">content</span>);<span style="color: #6a9955;">//This is a first sentence. This is an appended sentence.</span></div><div>&nbsp; &nbsp; });</div><div>}</div><div><span style="color: #9cdcfe;">document</span>.<span style="color: #dcdcaa;">addEventListener</span>(<span style="color: #ce9178;">"deviceready"</span>,<span style="color: #dcdcaa;">onDeviceReady</span>,<span style="color: #569cd6;">false</span>);</div>',
    },
    overloads:[
        {
            type:"(props:object,callback?:function,fallback?:function):Promise<SystemFile>",
            props:[SystemEntryPropsParam],
        },
        {
            type:"(fullpath:string,callback?:function,fallback?:function):Promise<SystemFile>",
            props:[
                SystemFileCallback,
                SystemEntryFallback,
                SystemFile,
            ],
        },
    ],
}
