import React,{useRef,useEffect,useState} from "react";
import {Accordion as VritraComponent} from "vritra";
import ReactDOM from "react-dom/client";


export default function Accordion(props){
    const {open,renderHeader,renderContent}=props;
    const state=useRef({
        parent:null,
        element:null,
        shadow:null,
        headerComponent:null,
        contentComponent:null,
    }).current;
    const [mounted,setMounted]=useState(false);
    useEffect(()=>{
        let {parent}=state;
        if(!parent){
            parent=state.parent=state.shadow.parentNode||document.body;
        }
        const {domref}=props;
        const element=VritraComponent({
            ...props,parent,open,
            renderHeader:renderHeader&&(({parent})=>{
                const component=state.headerComponent=ReactDOM.createRoot(parent);
                component.render(renderHeader({open}));
            }),
            renderContent:renderContent&&(({parent})=>{
                state.contentComponent?.unmount();
                const component=state.contentComponent=ReactDOM.createRoot(parent);
                component.render(renderContent());
            }),
        }).adjacentTo(state.element||state.shadow);
        state.element?.remove();
        state.element=element;
        if(domref){
            domref.current=element;
        }
        setMounted(true);
    },[]);
    useEffect(()=>{
        state.headerComponent?.render(renderHeader({open}));
    },[open]);
    /* useEffect(()=>{
        mounted&&state.element?.toggle(open);
    },[open]); */
    /* useEffect(()=>{
        const {element}=state;
        
    },[]); */
    
    return mounted?React.createElement(React.Fragment):React.createElement("div",{
        ref:(element)=>{state.shadow=element},
        style:{display:"inline"},
    });
};
