import React from "react";
import css from "./AddWebViewBlog.module.css";
import {BlogView,FileNavigator,FileView,NoticeView,SectionView,TextView,Title} from "Components";
import {book0, secondviewentryfile0,secondviewlocation0} from "Assets";


export default function AddWebViewBlog(){
    return (
        <BlogView className={css.addwebviewblog}>
            <SectionView
                title="adding webviews"
                text={`
                    The process of Adding webviews involves presenting a new cordova activity on <b>Android</b> 
                    and a new cordova ViewController on <b>iOS</b>.
                    @To accomplish such thing, we're going to use the
                    <a href="#cordovaplugins#webview">vritra-plugin-webview</a> plugin included by default when using
                    the <span>create-vritra-app</span> npx command to initialize your project. 
                `}
                //notice="This tutorial is using a cca (create-vritra-app) app."
            >
                <ol className={css.steplist}>
                    <li>
                        <Title title="create the webview entry :"/>
                        <TextView text={`
                            To define a new webview in the project, we start by creating the webview entry folder.
                            @Within the src\/WebViews directory, create a new folder named 
                            after the webview (e.g. <span>"SecondView"</span>). 
                            @Inside the <span>"SecondView"</span> folder, 
                            define the webview's component. This step involves creating 
                            the necessary files and implementing the functionalities that constitute 
                            the "SecondView" webview :
                        `}/>
                        <FileNavigator 
                            files={[
                                {name:"SecondView.js",content:statics.SecondView.js},
                                {name:"SecondView.module.css",content:statics.SecondView.css},
                            ]}
                            preview={secondviewlocation0}
                        />
                        <TextView text={`
                            then we create the webview entry file, <b>index.js</b> :
                        `}/>
                        <FileNavigator
                            files={[{content:statics.indexjs}]}
                            preview={secondviewentryfile0}
                        />
                        <NoticeView text={`
                            the <span>src\/WebViews\/index.js</span> file is used to target all webviews at once, that's why it should be 
                            imported in each webview entry file
                            @Each WebView entry folder <strong>must</strong> contain such index.js file
                        `}/>
                    </li>
                    <li>
                        <Title title="register the webview :"/>
                        <TextView text="After creating the webview's entry folder, we need to register the webview in the <b>WebViews.js</b> file :"/>
                        <FileView content={statics.WebViewsjs}/>
                        <TextView text={`
                            When registering a webview, the webview <span>name property</span>
                            <strong>must match</strong> the entry folder's name.
                            @Along with the name property, you can specify other webview's properties 
                            such the <b>statusBarColor</b>, <b>backgroundColor</b> and <a href="#cordovaplugins#webview">others</a>.
                            @The <span>id property</span> is used to specify an identifier for the webview. This id will be used to show the webview,
                            here is made default to the webview's name lowercased.
                            @The <span>file property</span> is used to specify a name for the html file in the <b>www</b> folder
                            for the webview. As you'll no longer deal with that folder that much, setting the value of this property won't
                            matter unless you need to, but it's still required.
                        `}/>
                        <NoticeView icon={book0} text={`
                            The <span>"backgroundColor:globalThis.backgroundColor"</span> sets a default value to the 
                            webview's backgroundColor property. The default value is determined 
                            by the css variable specified in the <span>src\/WebViews\/index.css</span> file.
                            @This value can be overwritten by specifying a backgroundColor value as shown above.
                        `}/>
                    </li>
                </ol>
            </SectionView>
            <SectionView title="showing webviews">
                <FileView content={statics.showWebViewCode}/>
                <TextView text={`
                    Given that we've registered a webview with the identifier 
                    <span>"secondview"</span>, invoking the show method and providing 
                    this identifier will display the webview 
                    along with the predefined options from its definition.
                `}/>
                <NoticeView icon={book0} text={`
                    We can overwrite the definition options by passing
                    new ones to the show method.
                `}/>
            </SectionView>
        </BlogView>
    )
}

const statics={
    showWebViewCode:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">View</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"vritra"</span>;</div><div><span style="color: #c586c0;">import</span> <span style="color: #9cdcfe;">css</span> <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"./MainView.module.css"</span>;</div><br><br><div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">MainView</span>(<span style="color: #9cdcfe;">props</span>){</div><div>    <span style="color: #569cd6;">const</span> {<span style="color: #4fc1ff;">parent</span>}=<span style="color: #9cdcfe;">props</span>;</div><div>    <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">mainview</span>=<span style="color: #dcdcaa;">View</span>({<span style="color: #9cdcfe;">parent</span>,<span style="color: #9cdcfe;">id</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"webview"</span>,<span style="color: #9cdcfe;">className</span><span style="color: #9cdcfe;">:css</span>.<span style="color: #9cdcfe;">mainview</span>});</div><br><div>    <span style="color: #4fc1ff;">mainview</span>.<span style="color: #9cdcfe;">innateHTML</span>=<span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">        &lt;button ref="showbtn"&gt;show SecondView&lt;/button&gt;</span></div><div><span style="color: #ce9178;">    `</span>;</div><div>    </div><div>    <span style="color: #4fc1ff;">mainview</span>.<span style="color: #9cdcfe;">showbtn</span>.<span style="color: #dcdcaa;">onclick</span>=()<span style="color: #569cd6;">=&gt;</span>{</div><div>        <span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">show</span>({<span style="color: #9cdcfe;">id</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"secondview"</span>});</div><div>    }</div><br><div>    <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">mainview</span>;</div><div>}</div>',
    WebViewsjs:'<div><span style="color: #4ec9b0;">module</span>.<span style="color: #4ec9b0;">exports</span><span style="color: #d4d4d4;">=</span>[</div><div>&nbsp; &nbsp; {</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"SecondView"</span>,</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">statusBarColor</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"#1e90ff"</span>,</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">backgroundColor</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"#bad8f5"</span>,</div><div>&nbsp; &nbsp; },</div><div>].<span style="color: #dcdcaa;">map</span>((<span style="color: #9cdcfe;">webview</span>,<span style="color: #9cdcfe;">i</span>)<span style="color: #569cd6;">=&gt;</span>({</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">backgroundColor</span><span style="color: #9cdcfe;">:globalThis</span>.<span style="color: #9cdcfe;">backgroundColor</span>,</div><div>&nbsp; &nbsp; <span style="color: #d4d4d4;">...</span><span style="color: #9cdcfe;">webview</span>,</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">id</span><span style="color: #9cdcfe;">:</span><span style="color: #9cdcfe;">webview</span>.<span style="color: #9cdcfe;">name</span>.<span style="color: #dcdcaa;">toLowerCase</span>(),</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">file</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">`index</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">i</span><span style="color: #d4d4d4;">+</span><span style="color: #b5cea8;">1</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">.html`</span>,</div><div>}));</div>',
    SecondView:{
        js:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">View</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"vritra"</span>;</div><div><span style="color: #c586c0;">import</span> <span style="color: #9cdcfe;">css</span> <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"./SecondView.module.css"</span>;</div><br><br><div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">SecondView</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> {<span style="color: #4fc1ff;">parent</span>}<span style="color: #d4d4d4;">=</span><span style="color: #9cdcfe;">props</span>;</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">secondview</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">View</span>({<span style="color: #9cdcfe;">parent</span>,<span style="color: #9cdcfe;">id</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"webview"</span>,<span style="color: #9cdcfe;">className</span><span style="color: #9cdcfe;">:css</span>.<span style="color: #9cdcfe;">secondview</span>});</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">secondview</span>.<span style="color: #9cdcfe;">innateHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;h3 class="</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">css</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">title</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">"&gt;My Second WebView&lt;/h3&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;button class="</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">css</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">closebtn</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">" ref="closebtn"&gt;close&lt;/button&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">secondview</span>.<span style="color: #9cdcfe;">closebtn</span>.<span style="color: #dcdcaa;">onclick</span><span style="color: #d4d4d4;">=</span>()<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">close</span>();</div><div>&nbsp; &nbsp; }</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">secondview</span>;</div><div>}</div>',
        css:'<div><span style="color: #d7ba7d;">.secondview</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">display</span>:<span style="color: #ce9178;">flex</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">flex-direction</span>:<span style="color: #ce9178;">column</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">justify-content</span>:<span style="color: #ce9178;">center</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">align-items</span>:<span style="color: #ce9178;">center</span>;</div><div>}</div><div><span style="color: #d7ba7d;">.title</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">font-size</span>:<span style="color: #b5cea8;">5em</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">margin-bottom</span>:<span style="color: #b5cea8;">0.75em</span>;</div><div>}</div><div><span style="color: #d7ba7d;">.closebtn</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">font-family</span>:<span style="color: #dcdcaa;">var</span>(<span style="color: #9cdcfe;">--mainFont</span>);</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">font-weight</span>:<span style="color: #b5cea8;">500</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">font-size</span>:<span style="color: #b5cea8;">3.733em</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">text-transform</span>:<span style="color: #ce9178;">capitalize</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">padding</span>:<span style="color: #b5cea8;">1em</span> <span style="color: #b5cea8;">2em</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">margin</span>:<span style="color: #b5cea8;">0.5em</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">border-radius</span>:<span style="color: #b5cea8;">0.5em</span>;</div><div>}</div>',
    },
    indexjs:'<div><span style="color: #c586c0;">import</span> <span style="color: #ce9178;">"../index"</span>;</div><div><span style="color: #c586c0;">import</span> <span style="color: #9cdcfe;">SecondView</span> <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"./SecondView"</span>;</div><br><br><div><span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">onDeviceReady</span>(){</div><div>&nbsp; &nbsp; <span style="color: #dcdcaa;">SecondView</span>({<span style="color: #9cdcfe;">parent</span><span style="color: #9cdcfe;">:</span><span style="color: #9cdcfe;">document</span>.<span style="color: #9cdcfe;">body</span>});</div><div>};</div><br><div><span style="color: #9cdcfe;">document</span>.<span style="color: #dcdcaa;">addEventListener</span>(<span style="color: #ce9178;">"deviceready"</span>,<span style="color: #dcdcaa;">onDeviceReady</span>,<span style="color: #569cd6;">false</span>);</div><br>',
}
