import {BlogView,FileView,NoticeView,SectionView} from "Components";


export default function AddJsBlog(props){
    const {}=props;
    return (
        <BlogView className={css.addjsblog}
            sections={[{
                title:"adding JavaScript",
                text:`
                    Certain elements on the screen may require updates in response to user input. 
                    For instance, clicking a button can toggle the visibility of certain elements 
                    or modify a quantity by increasing or decreasing it.
                    @In this page, you will learn how to add interaction to any vritra-themed component :
                `,
            }]}
        >
            <CounterExample/>
            <RerenderCpnBlog/>
            <BalancedArchExample/>
            <SectionView
                title="Vritra-Components structure:"
                text="After all what we have seen so far, a vritra component structure ends up to :"
            >
                <FileView content={statics.cpntskeletoncode}/>
            </SectionView>
            <NoticeView text={`
                for more examples, you can clone this <a href="https://github.com/AhmedAyachi/VritraExamples">github repository</a>
            `}/>
        </BlogView>
    )
}

