import React from "react";
import css from "./DataPassingBlog.module.css";
import {FileView,SectionView,BlogView, TextView, NoticeView} from "Components";
import {book0} from "Assets";


export default function DataPassingBlog(props){
    const {}=props;
    return (
        <BlogView className={css.datapassingblog}>
            <SectionView
                title="data passing"
                text={`
                    when navigating from a webview to another in your app, these webviews at some point will need to communicate 
                    with each other by sending data back and forth
                `}
            />
            <SectionView title="using messages">
                <TextView text={`
                    the options param of the <span>WebView.show</span> method contains a property named <b>"message"</b> that allows sending data 
                    to the next webview, the one that will be shown.
                `}/>
                <FileView content={statics.msgpropcode}/>
                <TextView>
                    if the message is not of type string, it's stringified and the shown webview 
                    can then consume it through:
                </TextView>
                <FileView content={statics.usemsgcode}></FileView>
                <NoticeView text={`
                    the message here is of type <b>String</b>, so make sure to 
                    <a href="#vritra#parsejson">parse</a> it if it's in json format.
                `}/>
                <TextView>to set the message value from the shown webview, we use:</TextView>
                <FileView content={statics.setmsgcode}></FileView>
                <TextView text={`
                    It's possible to set the message via the <span>WebView.close</span> method in one go, set and close instead of
                    calling the <span>setMessage</span> method and then calling <span>close</span>
                `}/>
                <FileView content={statics.closecode}></FileView>
                <TextView text={`
                    and then the initial webview can consume the message from the closed one 
                    via the <span>onClose</span> listener
                `}/>
                <FileView content={statics.onclosecode}/>
                <NoticeView text={`
                    Don't expect the <span>useMessage</span> method to get you the message value in this case, as the latter only
                    gets the message from the webview that opened the one in question
                `}/>
            </SectionView>
            <SectionView title="using the store">
                <TextView>
                    the message property effectively addresses the problem of transmitting a message from one webview to the next. 
                    However, what if we need to send a message to a webview that is not the immediate next one, or to any 
                    webview that came before the current one?
                    @this is where the store comes in. It allows storing data that is accessible from any webview
                    @to set the store initial value, we use:
                </TextView>
                <FileView content={statics.initiatestorecode}/>
                <NoticeView text={`
                    the value passed to the <span>initiateStore</span> method needs to be an object
                    @you generally need to call this method only in the main webview as it will overwrite 
                    the whole store value if called again
                `}/>
                <TextView>we can then set any store value through:</TextView>
                <FileView content={statics.setstorevaluecode}/>
                <TextView>we can target specific properties as well through:</TextView>
                <FileView content={statics.setstorenestedvaluecode}/>
                <TextView>and any webview can use the store value through:</TextView>
                <FileView editable="t" content={statics.usestorecode}/>
                <NoticeView icon={book0} text={`
                    the store value provided by the <span>useStore</span> method is of value type, which means setting that object
                    won't set the store value, you need to explicitly call the <span>setStore</span> method for that
                    @you should be aware of the fact that all the methods mentioned above are async
                    @the <b>WebView</b> object is provided by the <span>vritra-plugin-webview</span> 
                    included by default when using <span>create-vritra-app</span> npx command to create a new vritra-themed project,
                    but the plugin itself can be installed independently in any cordova project
                    <a href="#cordovaplugins#webview" target="_self">(learn more)</a>
                `}/>
            </SectionView>
        </BlogView>
    )
}

const statics={
    usestorecode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">useStore</span>(<span style="color: #9cdcfe;">store</span><span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; <span style="color: #6a9955;">/**</span></div><div><span style="color: #6a9955;">&nbsp; &nbsp; &nbsp;* use the store value here</span></div><div><span style="color: #6a9955;">&nbsp; &nbsp; */</span></div><div>});</div>',
    setstorenestedvaluecode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">setStore</span>(<span style="color: #ce9178;">"user.age"</span>,<span style="color: #b5cea8;">28</span>,(<span style="color: #9cdcfe;">store</span>)<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; <span style="color: #6a9955;">/**</span></div><div><span style="color: #6a9955;">&nbsp; &nbsp; &nbsp;* code after store set</span></div><div><span style="color: #6a9955;">&nbsp; &nbsp; */</span></div><div>});</div>',
    setstorevaluecode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">setStore</span>(<span style="color: #ce9178;">"user"</span>,{<span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"new user name"</span>,<span style="color: #9cdcfe;">age</span><span style="color: #9cdcfe;">:</span><span style="color: #b5cea8;">28</span>});</div>',
    initiatestorecode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">initiateStore</span>({</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">user</span><span style="color: #9cdcfe;">:</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"user name"</span>,</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">age</span><span style="color: #9cdcfe;">:</span><span style="color: #b5cea8;">27</span>,</div><div>&nbsp; &nbsp; },</div><div>});</div>',
    onclosecode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">show</span>({</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">id</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"webview_id"</span>,</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">message</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"hello world"</span>,</div><div>&nbsp; &nbsp; <span style="color: #dcdcaa;">onClose</span><span style="color: #9cdcfe;">:</span>({<span style="color: #9cdcfe;">message</span>})<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #6a9955;">/* message value from the closed webview */</span></div><div>&nbsp; &nbsp; },</div><div>});</div>',
    closecode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">close</span>(<span style="color: #ce9178;">"new message value"</span>);</div>',
    setmsgcode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">setMessage</span>(<span style="color: #ce9178;">"new message value"</span>);',
    usemsgcode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">useMessage</span>(<span style="color: #9cdcfe;">message</span><span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; <span style="color: #6a9955;">/* Your code goes here */</span></div><div>});</div>',
    msgpropcode:'<div><span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">show</span>({</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">id</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"webview_id"</span>,</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">message</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"hello world"</span>,</div><div>});</div>',
}
