import React from "react";
import css from "./RenderListBlog.module.css";
import {BlogView,FileNavigator,FileView,NoticeView,SectionView,TextView, Title} from "Components";
import {examples} from "Resources";
import {book0} from "Assets";


export default function RenderListBlog(props){
    const {}=props;
    return (
        <BlogView className={css.renderlistblog}>
            <SectionView
                title="rendering lists"
                text={`
                    you may frequently find the need to display multiple similar components based on a collection of data.
                    @You can use the vritra <b>map</b> function to manipulate an array of data.
                    On this page, you'll use map to transform your array of data into a html layout string.
                `}
            >
                <TextView text="Say that you have an array of produce to buy :"/>
                <FileView content={statics.producarraycode}/>
                <TextView text="to render the produce list, you just need to call map this way :"/>
                <FileNavigator
                    files={[{name:"ProduceList.js",content:statics.ProduceListJS}]}
                    previewClassName={css.producelistpreview}
                    preview={examples.ProduceExample.ProduceList}
                />
            </SectionView>
            <SectionView 
                title="why map over array.map method ?"
                text="Using array.map will generate the following :"
            >
                <FileNavigator
                    files={[{name:"ProduceList.js",content:statics.ProduceListWithArrayMapJS}]}
                    previewClassName={css.producelistpreview}
                    preview={examples.ProduceExample.ProduceListWithArrayMapJS}
                />
                <TextView text={`
                    As you can see, there are few commas that showed up between the <b>li</b> items.
                    @That's because in JavaScript, the string representation of an array of strings is <span>its items</span> separated
                    by <b>,</b> .
                `}/>
                <NoticeView
                    icon={book0}
                    text={`
                        The primary purpose of the <a href="#vritra#map">map</a> function provided 
                        by the vritra package is to tackle this problem. 
                        @it allows you to render multiple items simultaneously.
                    `}
                />
            </SectionView>
            <SectionView 
                title="Filtering an array of items"
                text="The produce array can be structured even more :"
            >
                <FileView content={statics.detailedproducearraycode}/>
                <TextView text={`
                    Let's say you want a way to only show produce whose type is "fruit".
                    You can use JavaScript's <span>filter()</span> method to return just those items
                    and then pass it to the <b>map</b> function.
                    @This is what it looks like :
                `}/>
                <FileNavigator
                    files={[{content:statics.fruitlistcode}]}
                    previewClassName={css.producelistpreview}
                    preview={examples.ProduceExample.FruitList}
                />
            </SectionView>
            <SectionView 
                title="Lazy Rendering items"
                text="Imagine having an array of items this long :"
            >
                <FileView content={statics.longarraycode}/>
                <TextView text={`
                    Rendering the entire list by mapping items from such an array can be resource-intensive, 
                    potentially causing your app to become unresponsive for a significant duration. 
                    @This occurs because JavaScript is occupied rendering all 100 elements, even though 
                    only the first five are needed.
                    @To tackle this issue, the vritra package provides a core component called 
                    <a href="#vritra#FlatList">FlatList</a> that enables the lazy rendering of items
                    as the user scrolls :
                `}/>
                <FileView content={statics.flatlistcode}/>
            </SectionView>
        </BlogView>
    )
}

const statics={
    flatlistcode:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">FlatList</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"vritra"</span>;</div><div><span style="color: #c586c0;">import</span> <span style="color: #9cdcfe;">ItemView</span> <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"./ItemView/ItemView"</span>;</div><br><br><div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">VeryLongList</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> {<span style="color: #4fc1ff;">parent</span>,<span style="color: #4fc1ff;">items</span>}<span style="color: #d4d4d4;">=</span><span style="color: #9cdcfe;">props</span>;</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">verylonglist</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">FlatList</span>({</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">parent</span>,</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">data</span><span style="color: #9cdcfe;">:</span><span style="color: #4fc1ff;">items</span>,</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">renderItem</span><span style="color: #dcdcaa;">:ItemView</span>,</div><div>&nbsp; &nbsp; });</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">verylonglist</span>.<span style="color: #9cdcfe;">beforeEndHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">verylonglist</span>;</div><div>}</div>',
    longarraycode:'<div><span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">items</span><span style="color: #d4d4d4;">=</span><span style="color: #569cd6;">new</span> <span style="color: #4ec9b0;">Array</span>(<span style="color: #b5cea8;">100</span>).<span style="color: #dcdcaa;">fill</span>().<span style="color: #dcdcaa;">map</span>((<span style="color: #9cdcfe;">_</span>,<span style="color: #9cdcfe;">i</span>)<span style="color: #569cd6;">=&gt;</span>({</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">id</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"item"</span><span style="color: #d4d4d4;">+</span>(<span style="color: #9cdcfe;">i</span><span style="color: #d4d4d4;">+</span><span style="color: #b5cea8;">1</span>),</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"item "</span><span style="color: #d4d4d4;">+</span>(<span style="color: #9cdcfe;">i</span><span style="color: #d4d4d4;">+</span><span style="color: #b5cea8;">1</span>),</div><div>}));</div>',
    ProduceListWithArrayMapJS:'<div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">ProduceList</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">producelist</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">View</span>({<span style="color: #d4d4d4;">...</span><span style="color: #9cdcfe;">props</span>,<span style="color: #9cdcfe;">tag</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"ul"</span>});</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">producelist</span>.<span style="color: #9cdcfe;">innateHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; </span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">.</span><span style="color: #dcdcaa;">map</span><span style="color: #d4d4d4;">(</span><span style="color: #9cdcfe;">item</span><span style="color: #569cd6;">=&gt;</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &lt;li&gt;</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">item</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">&lt;/li&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; `</span><span style="color: #d4d4d4;">)</span><span style="color: #569cd6;">}</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">producelist</span>;</div><div>}</div>',
    ProduceListJS:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">View</span>,<span style="color: #9cdcfe;">map</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"vritra"</span>;</div><br><br><div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">ProduceList</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">producelist</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">View</span>({<span style="color: #d4d4d4;">...</span><span style="color: #9cdcfe;">props</span>,<span style="color: #9cdcfe;">tag</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"ul"</span>});</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">producelist</span>.<span style="color: #9cdcfe;">innateHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; </span><span style="color: #569cd6;">${</span><span style="color: #dcdcaa;">map</span><span style="color: #d4d4d4;">(</span><span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">,(</span><span style="color: #9cdcfe;">item</span><span style="color: #d4d4d4;">)</span><span style="color: #569cd6;">=&gt;</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &lt;li&gt;</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">item</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">&lt;/li&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; `</span><span style="color: #d4d4d4;">)</span><span style="color: #569cd6;">}</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">producelist</span>;</div><div>}</div><br><div><span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">=</span>[</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"pear"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"potato"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"pineapple"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"cabbage"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"raspberry"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"peas"</span>,</div><div>];</div>',
    producarraycode:'<div><span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">=</span>[</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"pear"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"potato"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"pineapple"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"cabbage"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"raspberry"</span>,</div><div>&nbsp; &nbsp; <span style="color: #ce9178;">"peas"</span>,</div><div>];</div>',
    detailedproducearraycode:'<div><span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">=</span>[</div><div>&nbsp; &nbsp; {<span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"pear"</span>,<span style="color: #9cdcfe;">type</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"fruit"</span>},</div><div>&nbsp; &nbsp; {<span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"potato"</span>,<span style="color: #9cdcfe;">type</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"vegetable"</span>},</div><div>&nbsp; &nbsp; {<span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"pineapple"</span>,<span style="color: #9cdcfe;">type</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"fruit"</span>},</div><div>&nbsp; &nbsp; {<span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"cabbage"</span>,<span style="color: #9cdcfe;">type</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"vegetable"</span>},</div><div>&nbsp; &nbsp; {<span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"raspberry"</span>,<span style="color: #9cdcfe;">type</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"fruit"</span>},</div><div>&nbsp; &nbsp; {<span style="color: #9cdcfe;">name</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"peas"</span>,<span style="color: #9cdcfe;">type</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"vegetable"</span>},</div><div>];</div>',
    fruitlistcode:'<div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">FruitList</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">fruitlist</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">View</span>({<span style="color: #d4d4d4;">...</span><span style="color: #9cdcfe;">props</span>,<span style="color: #9cdcfe;">tag</span><span style="color: #9cdcfe;">:</span><span style="color: #ce9178;">"ul"</span>});</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">fruitlist</span>.<span style="color: #9cdcfe;">innateHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; </span><span style="color: #569cd6;">${</span><span style="color: #dcdcaa;">map</span><span style="color: #d4d4d4;">(</span><span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">.</span><span style="color: #dcdcaa;">filter</span><span style="color: #d4d4d4;">(</span><span style="color: #9cdcfe;">item</span><span style="color: #569cd6;">=&gt;</span><span style="color: #9cdcfe;">item</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">type</span><span style="color: #d4d4d4;">===</span><span style="color: #ce9178;">"fruit"</span><span style="color: #d4d4d4;">),({</span><span style="color: #9cdcfe;">name</span><span style="color: #d4d4d4;">})</span><span style="color: #569cd6;">=&gt;</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &lt;li&gt;</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">name</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">&lt;/li&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; `</span><span style="color: #d4d4d4;">)</span><span style="color: #569cd6;">}</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">fruitlist</span>;</div><div>}</div>',
}
