import React from "react";
import css from "./ProduceViewExample.module.css";
import {FileNavigator,BlogView,SectionView} from "Components";
import {examples} from "Resources";
import {book0} from "Assets";


export default function ProduceViewExample(props){
    const {}=props;
    return (
        <SectionView 
            className={css.produceviewexample}
            title="defining a ProduceView component"
        >
            <FileNavigator
                style={{alignItems:"center"}}
                files={[
                    {name:"ProduceView.js",content:statics.ProduceView.js},
                    {name:"ProduceView.css",content:statics.ProduceView.css},
                ]}
                preview={({parent})=>examples.ProduceExample.ProduceView({parent,produce:statics.produce})}
            />
            <BlogView 
                className={css.steps} 
                spacing={3}
                sections={statics.steps.map((step,i)=>({
                    title:`step ${i+1}: ${step.name}`,
                    text:step.description,
                    notice:step.notice,
                }))}
            />
        </SectionView>
    )
}

const statics=ProduceViewExample.statics={
    produce:examples.ProduceExample.produce.fruits[5],
    steps:[
        {
            name:"Import the View component",
            description:`
                The <a href="#vritra#View">View</a> component is a package core component that provides
                several useful methods and properties in addition to those attached to any 
                normal HTMLElement
            `,
        },
        {
            name:"Desctructure the component props",
            description:`
                Here, the ProduceView component has three props, two required <span>parent</span>, <span>produce</span> 
                and one optional <span>id</span>.
                @The <span>=randomId("produceview")</span> is a standard JavaScript syntax and it means: if props.id is
                <span>undefined</span> use the value specified.
            `,
            notice:{
                icon:book0,
                text:`
                    We can specify default prop values using simple JavaScript syntax as follows:<br>
                    <span>const</span> <span>{ </span>propname<span> = value</span><span> }</span> = props
                `,
            },
        },
        {
            name:"Add markup",
            description:`
                The <a href="#vritra#View">innateHTML</a> behaves as any innerHTML setter, so you can use it to add your desired html layout. Its only purpose
                is to prevent any JavaScript and hence <span>XSS attacks </span>. You can add interaction and event listeners directly after the innateHTML call. 
            `,
        },
        {
            name:"Return the component element",
            notice:{
                icon:book0,
                text:`
                    components do not have to essentially use the View component. It's just a simple Javascript function that takes 
                    at least one prop called <span>parent</span> and <span>returns</span> an HTMLElement .
                `,
            },
        },
    ],
    ProduceView:{
        js:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">randomId</span>,<span style="color: #9cdcfe;">View</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"vritra"</span>;</div><div><span style="color: #c586c0;">import</span> <span style="color: #9cdcfe;">css</span> <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"./ProduceView.module.css"</span>;</div><br><br><div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">ProduceView</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> {<span style="color: #4fc1ff;">parent</span>,<span style="color: #4fc1ff;">id</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">randomId</span>(<span style="color: #ce9178;">"produceview"</span>),<span style="color: #4fc1ff;">produce</span>}<span style="color: #d4d4d4;">=</span><span style="color: #9cdcfe;">props</span>;</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">produceview</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">View</span>({<span style="color: #9cdcfe;">parent</span>,<span style="color: #9cdcfe;">id</span>,<span style="color: #9cdcfe;">className</span><span style="color: #9cdcfe;">:</span><span style="color: #4fc1ff;">css</span>.<span style="color: #9cdcfe;">produceview</span>});</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">produceview</span>.<span style="color: #9cdcfe;">innateHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;div class="</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">css</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">background</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">" style="background-image:url(</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">image</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">)"&gt;&lt;/div&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;div class="</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">css</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">foreground</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">"&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &lt;img class="</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">css</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">image</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">" alt="</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">name</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">" src="</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">image</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">"/&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &lt;span class="</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">css</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">price</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">"&gt;</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">produce</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">price</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;"> $&lt;/span&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;/div&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">produceview</span>;</div><div>}</div>',
        css:'<div><span style="color: #d7ba7d;">.produceview</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">position</span>:<span style="color: #ce9178;">relative</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">width</span>:<span style="color: #b5cea8;">25em</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">height</span>:<span style="color: #b5cea8;">35em</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">border-radius</span>:<span style="color: #b5cea8;">2em</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">background-color</span>:<span style="color: #dcdcaa;">rgba</span>(<span style="color: #b5cea8;">0</span>,<span style="color: #b5cea8;">0</span>,<span style="color: #b5cea8;">0</span>,<span style="color: #b5cea8;">0.2</span>);</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">box-shadow</span>:<span style="color: #b5cea8;">0</span> <span style="color: #b5cea8;">0</span> <span style="color: #b5cea8;">15px</span> <span style="color: #dcdcaa;">rgba</span>(<span style="color: #b5cea8;">0</span>,<span style="color: #b5cea8;">0</span>,<span style="color: #b5cea8;">0</span>,<span style="color: #b5cea8;">0.35</span>);</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">overflow</span>:<span style="color: #ce9178;">hidden</span>;</div><div>}</div><div><span style="color: #d7ba7d;">.background</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">width</span>:<span style="color: #b5cea8;">100%</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">height</span>:<span style="color: #b5cea8;">100%</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">background-repeat</span>:<span style="color: #ce9178;">no-repeat</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">background-position</span>:<span style="color: #ce9178;">center</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">background-size</span>:<span style="color: #ce9178;">cover</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">filter</span>:<span style="color: #dcdcaa;">blur</span>(<span style="color: #b5cea8;">5em</span>);</div><div>}</div><div><span style="color: #d7ba7d;">.foreground</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">position</span>:<span style="color: #ce9178;">absolute</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">width</span>:<span style="color: #b5cea8;">100%</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">height</span>:<span style="color: #b5cea8;">100%</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">display</span>:<span style="color: #ce9178;">flex</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">flex-direction</span>:<span style="color: #ce9178;">column</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">justify-content</span>:<span style="color: #ce9178;">center</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">align-items</span>:<span style="color: #ce9178;">center</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">left</span>:<span style="color: #b5cea8;">0</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">top</span>:<span style="color: #b5cea8;">0</span>;</div><div>}</div><div><span style="color: #d7ba7d;">.image</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">width</span>:<span style="color: #b5cea8;">50%</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">margin-bottom</span>:<span style="color: #b5cea8;">3em</span>;</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">filter</span>:<span style="color: #dcdcaa;">drop-shadow</span>(<span style="color: #b5cea8;">0</span> <span style="color: #b5cea8;">0</span> <span style="color: #b5cea8;">20px</span> <span style="color: #ce9178;">black</span>);</div><div>}</div><div><span style="color: #d7ba7d;">.price</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">font-family</span>:<span style="color: #dcdcaa;">var</span>(<span style="color: #9cdcfe;">--majorFont</span>);</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">font-size</span>:<span style="color: #b5cea8;">5em</span>;</div><div>}</div>',
    },
}
