import React,{useState,useEffect,useRef,useMemo} from "react";
import {SideBarNavigator as VritraComponent} from "vritra";
import ReactDOM from "react-dom/client";


export default function SideBarNavigator(props){
    const {entryId,fallbackEntryId,sidebarShown}=props;
    const [mounted,setMounted]=useState(false);
    const state=useRef({
        shadow:null,
        element:null,
    }).current;
    const entries=useMemo(()=>mendEntries(props.entries),[props.entries]);
    useEffect(()=>{
        if(!mounted){
            const {shadow}=state,{domref}=props;
            const element=state.element=VritraComponent({
                ...props,entries,
                parent:shadow.parentNode,
            }).adjacentTo(shadow);
            if(typeof(domref)==="function") domref(element);
            else if(domref) domref.current=element;
            element.navigate(entryId);
            setMounted(true);
        }
    },[]);//[mounted,...statics.propkeys.map(key=>props[key]),entries]);
    useEffect(()=>{
        const {element}=state;
        if(element){
            const currentEntryId=element.getCurrentEntry()?.id;
            if(entryId!==currentEntryId){
                const didNavigate=element.navigate(entryId,false);
                if((!didNavigate)&&fallbackEntryId){
                    element.navigate(fallbackEntryId,false);
                }
            };
        }
    },[state.element,entryId,fallbackEntryId]);
    useEffect(()=>{
        const {element}=state;
        if(element){
            element.toggleSideBar(sidebarShown);
        }
    },[state.element,sidebarShown]);
    return mounted?React.createElement(React.Fragment):React.createElement("div",{
        ref:(element)=>{if(element){
            state.shadow=element;
        }},
    }); 
}

const statics={
    propkeys:[
        "id","className","sidebarClassName","containerClassName",
        "sideBarScrollTheme",
        "tintColor","folderColor","endpointColor","style",
    ],
}

const mendEntries=(entries)=>entries.map(item=>{
    const entry={...item};
    const {entries}=entry;
    if(entries){entry.entries=mendEntries(entries)}
    else if(entry.renderContent){
        const {renderContent}=entry;
        entry.renderContent=({parent})=>{
            const wrapperEl=document.createElement("div");
            wrapperEl.style.display="inline";
            const root=ReactDOM.createRoot(wrapperEl);
            root.render(renderContent({}));
            parent.appendChild(wrapperEl);
            return wrapperEl;
        }
    }
    return entry;
});
