import React from "react";
import css from "./AddScreenBlog.module.css";
import {BlogView,FileNavigator,FileView,NoticeView,SectionView,TextView,Title} from "Components";
import {screenslocation0,} from "Assets";


export default function AddScreenBlog(){
    return (
        <BlogView 
            className={css.addscreenblog}
            sections={[
                {
                    title:"what are screens ?",
                    text:`
                        In a mobile app, a <b>"screen"</b> refers to a visual interface 
                        or a distinct view that displays specific content or functionality 
                        to the user. Mobile apps are composed of multiple screens 
                        that users can navigate through to interact with different features
                        offered by the app.
                    `,
                },
                {
                    title:"Screens vs Webviews",
                    text:`
                        while screens and webviews may appear similar at first glance, 
                        they are slightly different. Although the process of creating 
                        either a screen or webview is essentially alike, each serves a distinct purpose.
                        @webViews essentially function as new Cordova activities on Android 
                        or as new Cordova ViewControllers on iOS.
                        @on the other hand, screens represent the visual interfaces. 
                        Within a single webview, multiple screens can be displayed. 
                        @For instance, consider the MainView, which acts as a webview, 
                        it can contain both Login and Home screens. The specific screen 
                        displayed depends on the user's logged-in status.
                    `,
                },
            ]}
        >
            <SectionView title="Creating Login and Home screens">
                <ol className={css.steps}>
                    <li>
                        <Title title="create the screen component"/>
                        <FileNavigator
                            style={{marginTop:"3.5em"}}
                            files={[
                                {name:"LoginScreen.js",content:statics.LoginScreenJS},
                                {name:"HomeScreen.js",content:statics.HomeScreenJS},
                            ]}
                            preview={screenslocation0}
                        />
                    </li>
                    <li>
                        <Title title="export the screen"/>
                        <TextView text={`
                            After creating the screen component, we export it in the index.js file: 
                        `}/>
                        <FileView className={css.fileview} content={statics.indexjs}/>
                    </li>
                </ol> 
            </SectionView>
            <SectionView title="Using screens">
                <FileView editable=" " content={statics.MainViewJS}/>
                <NoticeView text={`
                    All screens should be located in the <b>Screens</b> folder and need to be exported
                    from the <b>Screens\/index.js</b> file to be importable by the <span>"screens"</span>
                    import-alias.
                `}/>
            </SectionView>
        </BlogView>
    )
}

