import React from "react";
import css from "./RerenderCpnBlog.module.css";
import {SectionView,FileNavigator,FileView,NoticeView,TextView} from "Components";
import {examples} from "Resources";


export default function RerenderCpnBlog(props){
    const {}=props;
    return (
        <SectionView 
            className={css.rerendercpnblog}
            title="re-rendering components"
        >
            <TextView text={`
                In the <span>CounterView</span> example, we observed that clicking a button triggered the update of the count element. 
                This approach works well when we only need to update a single element at a time
                @However, what if we encounter scenarios where multiple updates are required based on various user interactions? 
                @As we continue to update the UI, it becomes increasingly cumbersome and impractical.
                We find ourselves writing repetitive lines of code to update multiple parts of the component 
                or even the entire component
                @Let's see this in action, let's say we have a to-do list:
            `}/>
            <FileView content={statics.todolistcode}/>
            <TextView text="let's define a ToDoItem component:"/>
            <FileNavigator
                files={[{content:statics.todoitemcode}]}
                previewClassName={css.todolist}
                preview={({parent})=>{
                    const {ToDoItem}=examples;
                    statics.todolist.forEach((item,i)=>{
                        ToDoItem({...item,parent,id:"todoitem"+(i+1)});
                    });
                }}
            />
            <TextView text={`
                When the checkbox is checked in this component, the entire layout should be updated. 
                @It is not practical to target each element individually, especially when there are multiple elements 
                or sub-components involved, which can be more than just two in real-world scenarios
                @The <span>substitute</span> method of the view serves the main purpose of <span>replacing</span> one element with another, 
                allowing us to re-render components
            `}/>
            <NoticeView text={`
                To maintain a balanced approach between individual updates and re-rendering, it is advisable 
                to avoid excessive usage of the <span>substitute</span> method. Its primary purpose is to re-render components, 
                offering a way to strike a <span>balance</span> between <span>individual updates</span> 
                and <span>comprehensive re-rendering</span>
            `}/>
        </SectionView>
    )
}

