import React,{useState,useRef,useEffect} from "react";
import {randomId,TabNavigator as NativeComponent} from "vritra";
import {renderToStaticMarkup} from "react-dom/server";


export default function TabNavigator(props){
    const [mounted,setMounted]=useState(false);
    const state=useRef({
        shadow:null,
        element:null,
        tabs:mendTabs(props.tabs),
    }).current;
    useEffect(()=>{
        if(!state.element){
            const {shadow}=state,{domref}=props;
            const element=NativeComponent({
                ...props,
                parent:shadow.parentNode,
                tabs:state.tabs,
            }).adjacentTo(shadow);
            if(domref) domref.current=element;
            delete state.shadow;
            state.element=element;
            setMounted(true);
        }
    },[...statics.propKeys.map(key=>props[key]),state.tabs]);
    return mounted?React.createElement(React.Fragment):React.createElement("div",{
        style:{display:"inline"},
        ref:(element)=>{state.shadow=element},
    });
}

const statics={
    propKeys:[
        "id","className","headerClassName","containerClassName",
        "style","tintColor","tabTextColor","memorize",
    ],
}

const mendTabs=(tabs)=>{
    tabs?.forEach(tab=>{
        let {renderTabContent}=tab;
        if(!renderTabContent){
            renderTabContent=tab.renderTabContent=tab.renderContent;
        }
        tab.renderContent=(itemProps)=>{
            const {parent,id=randomId("tab")}=itemProps;
            const reactEl=React.cloneElement(renderTabContent(itemProps),{id,key:id});
            parent.insertAdjacentHTML("beforeend",renderToStaticMarkup(reactEl));
            const element=parent.querySelector(`#${id}`);
            return element;
        }
    });
    return tabs;
}
