import React from "react";
import css from "./CounterExample.module.css";
import {FileNavigator,FileView,NoticeView,SectionView,TextView} from "Components";
import {examples} from "Resources";


export default function CounterExample(props){
    const {}=props;
    return (
        <SectionView className={css.counterexample}
            title="defining a CounterView component"
        >
            <FileNavigator
                files={[{content:statics.CounterViewJs}]}
                preview={examples.CounterView}
            />
            <TextView text={`
                You may observe that the code following the <span>innateHTML</span> call is a typical 
                vanilla JavaScript code. There is no automatic rerendering, no <span>useNonSense</span> hook,
                nothing but simple vanilla JavaScript
                @There is an interesting aspect to note here, we are directly accessing elements from the view. 
                Ordinarily, we would use the <span>querySelector</span> method for this purpose. 
                However, in this case, we can bypass that step because of the unique <b>ref</b> attribute, 
                which is recognized by the innateHTML setter
            `}/>
            <NoticeView text={`
                The <span>state</span> object is a basic JavaScript object designed to store data associated with the component. 
                Its purpose is to maintain clean and well-organized code. Instead of creating individual variables, 
                it is considered good practice to centralize these variables within the state object to easily track 
                the component's data. In this case, it is named <span>"state"</span>, but you have the flexibility 
                to choose any desired name for it
            `}/>
            <TextView text={`
                The <b>ref</b> attribute is an <span>ephemeral</span> attribute:
            `}/>
            <FileView content={statics.counterHTML}/>
            <TextView text="and this is what is rendered in the DOM <span>(Document Object Model)</span>:"/>
            <FileView content={statics.renderedHTML}/>
            <TextView text={`
                As you can notice, the <span>ref</span> attribute is not actually present in the DOM. 
                However, it is recognized by the <span>innateHTML</span> setter as a reference attribute. 
                Before the content is rendered, the ref attribute is removed, which is why it is considered ephemeral
                @The ref attributes are view scoped, they'll behave as intended no matter how many instances are created
                from a certain component:
            `}/>
            {/* a new attribute has been added to the button element. In fact, if a button element does not already 
                have a <span>type</span> attribute, it is automatically added. Similarly, if an <b>img</b> element 
                does not have an <span>alt</span> attribute, an alt attribute with the value <span>"img"</span> is inserted 
                <NoticeView text={`
                    Please do not rely on the <span>innateHTML</span> setter to add <span>type</span> and <span>alt</span>, specially alt, attributes
                    for you, it's good practice to include them in any <span>button</span> / <span>img</span-> element
                `}/> 
                <TextView text={`
                `}/>
            */}
            <div className={css.multicounters} ref={(parent)=>{if(parent){
                parent.innerHTML="";
                const {CounterView}=examples;
                const count=window.innerWidth<567?2:3;
                for(let i=0;i<count;i++){CounterView({parent})}
            }}}/>
        </SectionView>
    )
}

const statics={//type="button"
    renderedHTML:'<span style="color: #808080;">&lt;</span><span style="color: var(--mainColor)">button</span><span style="color: #808080;">&gt;</span>-<span style="color: #808080;">&lt;/</span><span style="color: var(--mainColor)">button</span><span style="color: #808080;">&gt;</span></div><div><span style="color: #808080;">&lt;</span><span style="color: var(--mainColor)">span</span> <span style="color: #9cdcfe;">class</span>=<span style="color: #ce9178;">"</span><span style="color: rgb(86, 156, 214);">${</span><span style="color: rgb(79, 193, 255);">css</span><span style="color: rgb(212, 212, 212);">.</span><span style="color: rgb(156, 220, 254);">count</span><span style="color: rgb(86, 156, 214);">}</span><span style="color: rgb(206, 145, 120);">"</span><span style="color: rgb(128, 128, 128);">&gt;</span><span style="color: rgb(86, 156, 214);">${</span><span style="color: rgb(79, 193, 255);">state</span><span style="color: rgb(212, 212, 212);">.</span><span style="color: rgb(156, 220, 254);">count</span><span style="color: rgb(86, 156, 214);">}</span><span style="color: rgb(128, 128, 128);">&lt;/</span><span style="color:var(--mainColor)">span</span><span style="color: rgb(128, 128, 128);">&gt;</span><br></div><div><span style="color: #808080;">&lt;</span><span style="color: var(--mainColor)">button</span><span style="color: #808080;">&gt;</span>+<span style="color: #808080;">&lt;/</span><span style="color: var(--mainColor)">button</span><span style="color: #808080;">&gt;</span>',
    counterHTML:'<span style="color: #808080;">&lt;</span><span style="color: var(--mainColor);">button</span> <span style="color: #9cdcfe;">ref</span>=<span style="color: #ce9178;">"decreasebtn"</span><span style="color: #808080;">&gt;</span>-<span style="color: #808080;">&lt;/</span><span style="color: var(--mainColor);">button</span><span style="color: #808080;">&gt;</span></div><div><span style="color: #808080;">&lt;</span><span style="color: var(--mainColor);">span</span> <span style="color: #9cdcfe;">ref</span>=<span style="color: #ce9178;">"countEl"</span> <span style="color: #9cdcfe;">class</span>=<span style="color: #ce9178;">"</span><span style="color: rgb(86, 156, 214);">${</span><span style="color: rgb(79, 193, 255);">css</span><span style="color: rgb(212, 212, 212);">.</span><span style="color: rgb(156, 220, 254);">count</span><span style="color: rgb(86, 156, 214);">}</span><span style="color: rgb(206, 145, 120);">"</span><span style="color: rgb(128, 128, 128);">&gt;</span><span style="color: rgb(86, 156, 214);">${</span><span style="color: rgb(79, 193, 255);">state</span><span style="color: rgb(212, 212, 212);">.</span><span style="color: rgb(156, 220, 254);">count</span><span style="color: rgb(86, 156, 214);">}</span><span style="color: rgb(128, 128, 128);">&lt;/</span><span style="color:var(--mainColor)">span</span><span style="color: rgb(128, 128, 128);">&gt;</span><br></div><div><span style="color: #808080;">&lt;</span><span style="color: var(--mainColor);">button</span> <span style="color: #9cdcfe;">ref</span>=<span style="color: #ce9178;">"increasebtn"</span><span style="color: #808080;">&gt;</span>+<span style="color: #808080;">&lt;/</span><span style="color: var(--mainColor);">button</span><span style="color: #808080;">&gt;</span>',
    CounterViewJs:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">randomId</span>,<span style="color: #9cdcfe;">View</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"vritra"</span>;</div><div><span style="color: #c586c0;">import</span> <span style="color: #9cdcfe;">css</span> <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"./CounterView.module.css"</span>;</div><br><br><div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">CounterView</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> {<span style="color: #4fc1ff;">parent</span>,<span style="color: #4fc1ff;">id</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">randomId</span>(<span style="color: #ce9178;">"counterview"</span>)}<span style="color: #d4d4d4;">=</span><span style="color: #9cdcfe;">props</span>;</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">counterview</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">View</span>({<span style="color: #9cdcfe;">parent</span>,<span style="color: #9cdcfe;">id</span>,<span style="color: #9cdcfe;">className</span><span style="color: #9cdcfe;">:</span><span style="color: #4fc1ff;">css</span>.<span style="color: #9cdcfe;">counterview</span>}),<span style="color: #4fc1ff;">state</span><span style="color: #d4d4d4;">=</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">count</span><span style="color: #9cdcfe;">:</span><span style="color: #b5cea8;">20</span>,</div><div>&nbsp; &nbsp; };</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">counterview</span>.<span style="color: #9cdcfe;">innateHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;button ref="decreasebtn"&gt;-&lt;/button&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;span ref="countEl" class="</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">css</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">count</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">"&gt;</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">state</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">count</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">&lt;/span&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;button ref="increasebtn"&gt;+&lt;/button&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> {<span style="color: #4fc1ff;">increasebtn</span>,<span style="color: #4fc1ff;">decreasebtn</span>,<span style="color: #4fc1ff;">countEl</span>}<span style="color: #d4d4d4;">=</span><span style="color: #4fc1ff;">counterview</span>;</div><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">decreasebtn</span>.<span style="color: #dcdcaa;">onclick</span><span style="color: #d4d4d4;">=</span>()<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #4fc1ff;">state</span>.<span style="color: #9cdcfe;">count</span><span style="color: #d4d4d4;">--</span>;</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #4fc1ff;">countEl</span>.<span style="color: #9cdcfe;">innerHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #4fc1ff;">state</span>.<span style="color: #9cdcfe;">count</span>;</div><div>&nbsp; &nbsp; }</div><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">increasebtn</span>.<span style="color: #dcdcaa;">onclick</span><span style="color: #d4d4d4;">=</span>()<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #4fc1ff;">state</span>.<span style="color: #9cdcfe;">count</span><span style="color: #d4d4d4;">++</span>;</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #4fc1ff;">countEl</span>.<span style="color: #9cdcfe;">innerHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #4fc1ff;">state</span>.<span style="color: #9cdcfe;">count</span>;</div><div>&nbsp; &nbsp; }</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">counterview</span>;</div><div>}</div>',
}
