import {SystemEntryFallback,SystemEntryPropsParam} from "../SystemEntry";
import SystemFolder,{SystemFolderCallback} from "./SystemFolder";


export default {
    id:"useSystemFolder",
    type:"function",
    description:`
        A simpler way to use cordova-plugin-file API 
        @gets a system folder and creates it if not found
        @Browser support is for development only. Passive (does nothing)
        @requires <a href="https://cordova.apache.org/docs/en/12.x/reference/cordova-plugin-file/index.html">cordova-plugin-file</a>
    `,
    preview:{
        title:"typical usage",
        code:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">useSystemFolder</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"cordova-vritra"</span>;</div><br><br><div><span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">onDeviceReady</span>(){</div><div>&nbsp; &nbsp; <span style="color: #dcdcaa;">useSystemFolder</span>(<span style="color: #ce9178;">`</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">cordova</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">file</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">externalDataDirectory</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">/NewFolder`</span>,<span style="color: #569cd6;">async</span> (<span style="color: #9cdcfe;">sysfolder</span>)<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">sysfile</span><span style="color: #d4d4d4;">=</span><span style="color: #c586c0;">await</span> <span style="color: #9cdcfe;">sysfolder</span>.<span style="color: #dcdcaa;">useFile</span>(<span style="color: #ce9178;">"TextFile.txt"</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #4fc1ff;">sysfile</span>.<span style="color: #dcdcaa;">write</span>(<span style="color: #ce9178;">"This is a first sentence."</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #4fc1ff;">sysfile</span>.<span style="color: #dcdcaa;">append</span>(<span style="color: #ce9178;">"This is an appended sentence."</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">subfolder</span><span style="color: #d4d4d4;">=</span><span style="color: #c586c0;">await</span> <span style="color: #9cdcfe;">sysfolder</span>.<span style="color: #dcdcaa;">useFolder</span>(<span style="color: #ce9178;">"SubFolder"</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #4fc1ff;">sysfile</span>.<span style="color: #dcdcaa;">moveTo</span>(<span style="color: #4fc1ff;">subfolder</span>.<span style="color: #9cdcfe;">fullpath</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #4fc1ff;">subfolder</span>.<span style="color: #dcdcaa;">copyTo</span>(<span style="color: #9cdcfe;">cordova</span>.<span style="color: #9cdcfe;">file</span>.<span style="color: #9cdcfe;">externalCacheDirectory</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">subfoldercopy</span><span style="color: #d4d4d4;">=</span><span style="color: #c586c0;">await</span> <span style="color: #dcdcaa;">useSystemFolder</span>(<span style="color: #ce9178;">`</span><span style="color: #569cd6;">${</span><span style="color: #9cdcfe;">cordova</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">file</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">externalCacheDirectory</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">/</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">subfolder</span><span style="color: #d4d4d4;">.</span><span style="color: #9cdcfe;">name</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">`</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #4fc1ff;">subfoldercopy</span>.<span style="color: #dcdcaa;">rename</span>(<span style="color: #ce9178;">"SubFolderCopy"</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">sysfilecopy</span><span style="color: #d4d4d4;">=</span><span style="color: #c586c0;">await</span> <span style="color: #4fc1ff;">subfoldercopy</span>.<span style="color: #dcdcaa;">useFile</span>(<span style="color: #4fc1ff;">sysfile</span>.<span style="color: #9cdcfe;">name</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #4fc1ff;">sysfilecopy</span>.<span style="color: #dcdcaa;">rename</span>(<span style="color: #ce9178;">"TextFileCopy"</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #6a9955;">//code after all set</span></div><div>&nbsp; &nbsp; });</div><div>}</div><div><span style="color: #9cdcfe;">document</span>.<span style="color: #dcdcaa;">addEventListener</span>(<span style="color: #ce9178;">"deviceready"</span>,<span style="color: #dcdcaa;">onDeviceReady</span>,<span style="color: #569cd6;">false</span>);</div>',
    },
    overloads:[
        {
            type:"(props:object,callback?:function,fallback?:function):Promise<SystemFolder>",
            props:[SystemEntryPropsParam],
        },
        {
            type:"(path:string,callback?:function,fallback?:function):Promise<SystemFolder>",
            props:[
                SystemFolderCallback,
                SystemEntryFallback,
                SystemFolder,
            ],
        },
    ],
}
