import React from "react";
import css from "./FragmentBlog.module.css";
import Blog from "./Blog/Blog";
import {BlogView,FileNavigator,FileView,NoticeView,SectionView,TextView,Title} from "Components";
import {book0} from "Assets";


export default function FragmentBlog(props){
    const {}=props;
    return (
        <BlogView className={css.fragmentblog}>
            <SectionView
                title="creating fragments"
                text={`
                    a Fragment is a built-in component that allows you to 
                    group multiple elements together without adding an additional wrapping element to the DOM. 
                `}
            >
                <FileView content={statics.myfragmentcode}/>
                <TextView text={`
                    As you can see, using a <a href="#/vritra#Fragment">Fragment</a> is 
                    as simple as using the <span>View</span> component.
                `}/>
            </SectionView>
            <SectionView title="creating a Post component">
                <FileView name="Post.js" content={statics.PostJS}/>
                <FileNavigator 
                    files={[{name:"Blog.js",content:statics.BlogJs}]}
                    preview={Blog}
                />
                <TextView text={`
                    if you inspect this example with the browser tools, you'll see that all <span>h3</span> 
                    and <span>p</span> DOM nodes appear as siblings without wrappers around them.
                `}/>
                <NoticeView
                    icon={book0}
                    text={`
                        Nesting Fragments is similar to nesting Views. 
                        @It is possible to nest a Fragment within a View component, and vice versa.
                    `}
                />
            </SectionView>
        </BlogView>
    )
}

