import React from "react";
import css from "./StoreActionBlog.module.css";
import {BlogView,FileNavigator,FileView,NoticeView,SectionView,TextView} from "Components";
import {storelocation0} from "Assets";


export default function StoreActionBlog(){
    return (
        <BlogView className={css.storeactionblog}>
            <SectionView
                title="store actions"
                text={`
                    Store actions are simply functions that call the <span>WebView.setStore</span> method
                    @Instead of using WebView.setStore directly in your code, it's 
                    better to define an action and use that action to mutate the store
                `}
            >
                <FileNavigator 
                    files={[{content:statics.basicaction}]}
                    preview={storelocation0}
                />
                <TextView>
                    In some cases, setting a store value requires additional actions, such as saving 
                    changes to local storage or to an internal storage file:
                </TextView>
                <FileView content={statics.withfileaction}/>
                <NoticeView text={`
                    only store actions exported from the <span>src/Store/Actions/index.js</span> file 
                    are importable via the <span>"actions"</span> import alias
                `}/>
            </SectionView>
        </BlogView>
    )
}

const statics={
    withfileaction:'<div><span style="color: #c586c0;">import</span> {<span style="color: #9cdcfe;">useSystemFile</span>} <span style="color: #c586c0;">from</span> <span style="color: #ce9178;">"cordova-vritra"</span>;</div><br><br><div><span style="color: #c586c0;">export</span> <span style="color: #569cd6;">const</span> <span style="color: #dcdcaa;">setSomething</span><span style="color: #d4d4d4;">=</span>(<span style="color: #9cdcfe;">value</span>,<span style="color: #9cdcfe;">callback</span>)<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">WebView</span>.<span style="color: #dcdcaa;">setStore</span>(<span style="color: #ce9178;">"something"</span>,<span style="color: #9cdcfe;">value</span>,<span style="color: #569cd6;">async</span> ()<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">sysfile</span><span style="color: #d4d4d4;">=</span><span style="color: #c586c0;">await</span> <span style="color: #dcdcaa;">useSystemFile</span>(<span style="color: #ce9178;">"someFolderPath/File.json"</span>);</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #c586c0;">await</span> <span style="color: #4fc1ff;">sysfile</span>.<span style="color: #dcdcaa;">write</span>(<span style="color: #9cdcfe;">JSON</span>.<span style="color: #dcdcaa;">stringify</span>(<span style="color: #9cdcfe;">value</span>));</div><div>&nbsp; &nbsp; &nbsp; &nbsp; <span style="color: #9cdcfe;">callback</span><span style="color: #d4d4d4;">&amp;&amp;</span><span style="color: #dcdcaa;">callback</span>();</div><div>&nbsp; &nbsp; });</div><div>}</div>',
    basicaction:'<div><span style="color: #c586c0;">export</span> <span style="color: #569cd6;">const</span> <span style="color: #dcdcaa;">setSomething</span><span style="color: #d4d4d4;">=</span>(<span style="color: #9cdcfe;">value</span>,<span style="color: #9cdcfe;">callback</span>)<span style="color: #569cd6;">=&gt;</span>{</div><div>&nbsp; &nbsp; <span style="color: #9cdcfe;">WebView</span>.<span style="color: #dcdcaa;">setStore</span>(<span style="color: #ce9178;">"something"</span>,<span style="color: #9cdcfe;">value</span>,<span style="color: #9cdcfe;">callback</span>);</div><div>}</div>',
}
