import React from "react";
import css from "./UserInputBlog.module.css";
import {FileView,BlogView,TextView,Title,NoticeView, SectionView} from "Components";
import {book0} from "Assets";


export default function UserInputBlog(props){
    const {}=props;
    return (
        <BlogView className={css.userinputblog}>
            <SectionView
                title="Dealing with user input"
                text={`
                    Precautions must be taken regarding user input to prevent potential malicious actions, 
                    such as exposing apps to <span>XSS</span> attacks.@In vanilla JavaScript, it is crucial to avoid 
                    inserting user inputs using the <span>innerHTML</span> method, as it treats the input as an HTML string, 
                    making it susceptible to malicious JavaScript injection. Instead, the safer alternative is to 
                    utilize the <span>innerText</span> method as follows : 
                `}
            >
                <FileView content={statics.innerTextCode}/>
                <TextView text="This is the typical way of safely dealing with user input"/>
            </SectionView>
            <SectionView 
                title="using innateHTML"
                text="Imagine dealing with a user input such the one below : "
            >
                <FileView content={statics.maliciousInput}/>
                <TextView text="inserting such string inside the <span>innateHTML</span> method, will produce the following layout :"/>
                <FileView content={statics.innateHTMLLayout}/>
                <TextView text={`
                    The <b>img</b> element has been excluded because ,to innateHTML, any element that contains any attribute starting with
                    "on" is not allowed to prevent event listeners and hence JavaScript.
                    @The <b>script</b> was not rendered because such tag is disallowed.
                    @For <b>iframes</b>, innateHTML always sandboxes them.
                `}/>
                <NoticeView 
                    icon={book0}
                    text={`To insert custom iframes, you can use a <span>View</span> with <span>"iframe"</span> as a tag value`}
                />
                <TextView text={`
                    Although we have successfully prevented the XSS attacks mentioned earlier, there are situations 
                    where user inputs might appear malicious even though they have no malicious intent. 
                    This can occur when users share code snippets or similar content.
                    @To safely handle such scenarios, we use the <span>text</span> tag :
                `}/>
                <FileView content={statics.textTagCode}/>
                <TextView text={`
                    By using the <span>text</span> tag, we're telling the innateHTML method to treat the layout
                    inside the text element as plain text and therefore it calls innerText for us and renders the following :
                `}/>
                <FileView content={statics.textTagRenderCode}/>
                <NoticeView text={`
                    As <span>text</span> is not a legitimate HTML tag, <span>innateHTML</span> replaces the element with
                    a paragraph element by default.
                    @To specify an element with which the text element should be replaced, use the <span>as</span> attribute.
                    @for example, <span>as="h3"</span> will render an h3 element instead of the paragraph element in the layout above.
                    @The <span>as</span> attribute is ephemeral which means, it's removed before rendering.
                `}/>
            </SectionView>
        </BlogView>
    )
}

const statics={//</img>
    textTagRenderCode:`<span style="color: #808080;">&lt;</span><span style="color: #569cd6;">p</span><span style="color: #808080;">&gt;</span></div><div><div><span style="color: #ce9178;">    "&lt;img src="" onerror="alert('successful XSS attack')"&gt;"</span></div><div><span style="color: #ce9178;">    "&lt;iframe src="PathToMaliciousFile.html"&gt;&lt;/iframe&gt;"</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; "&lt;script src="PathToMaliciousCode.js"&gt;&lt;/script&gt;"</span></div></div><div><span style="color: #808080;">&lt;/</span><span style="color: #569cd6;">p</span><span style="color: #808080;">&gt;</span></div>`,
    textTagCode:'<div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">Component</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> {<span style="color: #4fc1ff;">parent</span>,<span style="color: #4fc1ff;">userinput</span>}<span style="color: #d4d4d4;">=</span><span style="color: #9cdcfe;">props</span>;</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">component</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">View</span>({<span style="color: #9cdcfe;">parent</span>});</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">component</span>.<span style="color: #9cdcfe;">innateHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;text&gt;</span><span style="color: #569cd6;">${</span><span style="color: #4fc1ff;">userinput</span><span style="color: #569cd6;">}</span><span style="color: #ce9178;">&lt;/text&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">component</span>;</div><div>}',
    innateHTMLLayout:'<span style="color: #808080;">&lt;</span><span style="color: var(--mainColor);">iframe</span> <span style="color: #9cdcfe;">src</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">"PathToMaliciousFile.html"</span> <span style="color: #9cdcfe;">sandbox</span><span style="color: #808080;">&gt;&lt;/</span><span style="color: var(--mainColor);">iframe</span><span style="color: #808080;">&gt;</span>',
    maliciousInput:'<div><span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">userinput</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &lt;img src="" onerror="alert(\'successful XSS attack\')"/&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &lt;iframe src="PathToMaliciousFile.html"&gt;&lt;/iframe&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &lt;script src="PathToMaliciousCode.js"&gt;&lt;/script&gt;</span></div><div><span style="color: #ce9178;">`</span>;</div>',
    innerTextCode:'<div><span style="color: #c586c0;">export</span> <span style="color: #c586c0;">default</span> <span style="color: #569cd6;">function</span> <span style="color: #dcdcaa;">Component</span>(<span style="color: #9cdcfe;">props</span>){</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> {<span style="color: #4fc1ff;">parent</span>,<span style="color: #4fc1ff;">userinput</span>}<span style="color: #d4d4d4;">=</span><span style="color: #9cdcfe;">props</span>;</div><div>&nbsp; &nbsp; <span style="color: #569cd6;">const</span> <span style="color: #4fc1ff;">component</span><span style="color: #d4d4d4;">=</span><span style="color: #dcdcaa;">View</span>({<span style="color: #9cdcfe;">parent</span>});</div><br><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">component</span>.<span style="color: #9cdcfe;">innateHTML</span><span style="color: #d4d4d4;">=</span><span style="color: #ce9178;">`</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; &nbsp; &nbsp; &lt;div ref="textEl" contenteditable="true"&gt;&lt;/div&gt;</span></div><div><span style="color: #ce9178;">&nbsp; &nbsp; `</span>;</div><div>&nbsp; &nbsp; </div><div>&nbsp; &nbsp; <span style="color: #4fc1ff;">component</span>.<span style="color: #9cdcfe;">textEl</span>.<span style="color: #9cdcfe;">innerText</span><span style="color: #d4d4d4;">=</span><span style="color: #4fc1ff;">userinput</span>;</div><br><div>&nbsp; &nbsp; <span style="color: #c586c0;">return</span> <span style="color: #4fc1ff;">component</span>;</div><div>}</div>',
}
